.form {
  display: flex;
  flex-direction: column;
  row-gap: 1.7rem;
  align-items: center;

  &--checkout {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 5rem;
  }

  &__input {
    font-family: inherit;
    line-height: inherit;
    letter-spacing: 0.03em;
    padding: .6rem 0 .6rem 1rem;
    border: none;
    border-bottom: 1.2px solid $color-primary;
    border-radius: 0;
    width: 100%;


    &::placeholder {
      color: $color-primary;
    }
    &:last-of-type {
      margin-bottom: 2rem;
    }
  }

  &__select-input {
    padding: 1rem;
    border:  1.1px solid $color-primary;
  }
  .form-group--full{
    grid-column: 1 / -1;
  }
}